import React, { useState, useContext, useEffect, useRef } from 'react';
import './Task30.css';
import { Context } from '../../Context';
import { HeadingText } from '../HeadingText/HeadingText';
import soundSource from '../Task27/sound/041.mp3';


//    - 
const alphabetModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>  </p>
        </>
    );
};

//    - 
const helpModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>     </p>
        </>
    );
};


//   ( html )
// START
//   -   ( <>     </> )
const lessonHeading = () => (
    <>
        <p>. Exercises.</p>
    </>
);
//      ( <>     </> )
const themeHeading = () => (
    <>
        <p>Choose the article if necessary</p>
    </>
);
//  title   (  html )
const titleHeading = '   ';


const SentenceWithSelect = ({ choise, setChoise, secondPartSentence, targetArticleValue, targetArticleContent, lessonSetStartAction }) => (
    <p className="lessonContent_chooseArticle_sentence">
        <select className="lessonContent_select" value={choise} onChange={(event) => { setChoise(event.target.value); lessonSetStartAction(true) }} title="  ">
            <option defaultValue value="outChoise">...</option>
            <option value={targetArticleValue.are}>{targetArticleContent(targetArticleValue.are)}</option>
            <option value={targetArticleValue.is}>{targetArticleContent(targetArticleValue.is)}</option>
            <option value={targetArticleValue.am}>{targetArticleContent(targetArticleValue.am)}</option>
        </select>&ensp;{secondPartSentence}?
    </p>
);

function Task30() {
    const { helpSetIcon, alphabetSetIcon, helpSetModalContent, alphabetSetModalContent, lessonSetStartAction, lessonSetResetTask, resetTask, lessonSetCheckTask, checkTask, lessonSetAndAnswer } = useContext(Context);
    //     - (  )
    useEffect(() => {
        //  helpSetIcon -    ( - true,  - false)
        helpSetIcon(true);
        //  alphabetSetIcon -   ( - true,  - false)
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, []);
    const selectContent = useRef(null);
    const [choise1, setChoise1] = useState(undefined);
    const [choise2, setChoise2] = useState(undefined);
    const [choise3, setChoise3] = useState(undefined);
    const [choise4, setChoise4] = useState(undefined);
    const [choise5, setChoise5] = useState(undefined);



    useEffect(() => {
        if (resetTask === true) {
            const arr = selectContent.current.childNodes;
            const resetStates = () => {
                setChoise1(undefined);
                setChoise2(undefined);
                setChoise3(undefined);
                setChoise4(undefined);
                setChoise5(undefined);
            };
            for (let i = 0; i < arr.length; i++) {
                console.log(arr[i].childNodes[0])
                arr[i].childNodes[0].selectedIndex = 0;
            }
            resetStates();
        }
        lessonSetResetTask(false);
    }, [resetTask]);

    useEffect(() => {
        if (checkTask === true) {
            if (choise1 === targetArticleValue.is && choise2 === targetArticleValue.are && choise3 === targetArticleValue.am && choise4 === targetArticleValue.are && choise5 === targetArticleValue.are) {
                lessonSetAndAnswer(true);
            }
            else {
                lessonSetAndAnswer(false);
            }
        }
        lessonSetCheckTask(false);
    }, [checkTask]);


    const targetArticleValue = { are: 'are', is: 'is', am: 'am' };
    const targetArticleContent = (targetArticle) => {
        return targetArticle.charAt(0).toUpperCase() + targetArticle.slice(1)
    }


    //      
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                titleHeading={titleHeading}
                soundSource={soundSource}
                soundVisible={true} titleVisible={true}
            />
            <div className="lessonContent_chooseArticle" ref={selectContent}>
                <SentenceWithSelect choise={choise1} setChoise={setChoise1} secondPartSentence='it her castle' targetArticleValue={targetArticleValue} targetArticleContent={targetArticleContent} lessonSetStartAction={lessonSetStartAction} />
                <SentenceWithSelect choise={choise2} setChoise={setChoise2} secondPartSentence='these my cats' targetArticleValue={targetArticleValue} targetArticleContent={targetArticleContent} lessonSetStartAction={lessonSetStartAction} />
                <SentenceWithSelect choise={choise3} setChoise={setChoise3} secondPartSentence='I a happy witch' targetArticleValue={targetArticleValue} targetArticleContent={targetArticleContent} lessonSetStartAction={lessonSetStartAction} />
                <SentenceWithSelect choise={choise4} setChoise={setChoise4} secondPartSentence='bats in the classroom' targetArticleValue={targetArticleValue} targetArticleContent={targetArticleContent} lessonSetStartAction={lessonSetStartAction} />
                <SentenceWithSelect choise={choise5} setChoise={setChoise5} secondPartSentence='we two spiders' targetArticleValue={targetArticleValue} targetArticleContent={targetArticleContent} lessonSetStartAction={lessonSetStartAction} />
            </div>
        </div>
    );
}

export default Task30;



