import React, { useState, useContext, useEffect, useMemo, useRef } from 'react';
import './Task23.css';
import { Context } from '../../Context';
import { HeadingText } from '../HeadingText/HeadingText';
import soundSource from './sound/038.mp3';


//    - 
const alphabetModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>  </p>
        </>
    );
};

//    - 
const helpModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>     </p>
        </>
    );
};


//   ( html )
// START
//   -   ( <>     </> )
const lessonHeading = () => (
    <>
        <p> ! Learn the ABC!</p>
    </>
);
//      ( <>     </> )
const themeHeading = () => (
    <>
        <p>Find, click on the letter u in the words and colour it red</p>
    </>
);
//  title   (  html )
const titleHeading = ',            .';

const FindLetterContent = ({ findLetter, handler, letterBox }) => (
    <div className="lessonContent_findLatter_U_Wrapper" ref={letterBox}>
        {findLetter.map((item, index) => {
            let style = {};
            if (item != ',' && item != ' ') {
                style = {
                    cursor: 'pointer',
                }
            }
            else {
                style = {
                    cursor: 'default',
                }
            }
            return (
                <span key={`${item}${index}`} onClick={item != ',' && item != ' ' ? handler : undefined} style={style} >{item}</span>
            )
        })}
    </div>
);

function Task23() {
    const { helpSetIcon, alphabetSetIcon, helpSetModalContent, alphabetSetModalContent, lessonSetResetTask, resetTask, lessonSetCheckTask, checkTask, lessonSetAndAnswer, lessonSetStartAction } = useContext(Context);
    //     - (  )
    useEffect(() => {
        //  helpSetIcon -    ( - true,  - false)
        helpSetIcon(true);
        //  alphabetSetIcon -   ( - true,  - false)
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, []);
    const letterBox = useRef(null);


    useEffect(() => {
        const letters = letterBox.current;
        for (let i = 0; i < letters.childNodes.length; i++) {
            if (letters.childNodes[i].classList.contains('matchColor_U')) {
                letters.childNodes[i].classList.remove('matchColor_U');
            }
        }

        lessonSetResetTask(false);
    }, [resetTask]);



    useEffect(() => {
        if (checkTask === true) {
            let letterNumber = () => {
                return findLetter.reduce((count, elem) => count += elem === targetLetter.toLowerCase() || elem === targetLetter.toUpperCase(), 0);
            };
            const letters = letterBox.current;
            let winCount = 0;
            let allMached = 0;
            for (let i = 0; i < letters.childNodes.length; i++) {
                if (letters.childNodes[i].classList.contains('matchColor_U')) {
                    allMached++;
                }
                if (letters.childNodes[i].innerHTML.toLowerCase() === targetLetter.toLowerCase() && letters.childNodes[i].classList.contains('matchColor_U')) {
                    winCount++;
                }
            }
            if (allMached === letterNumber() && winCount === letterNumber()) {
                lessonSetAndAnswer(true);
            }
            else {
                lessonSetAndAnswer(false);
            }

        }
        lessonSetCheckTask(false);
    }, [checkTask]);

    const targetLetter = 'u';
    const findLetterContent = 'continue, fruit, blue, pupil, sun, run, nut, touch, young, cue';
    const findLetter = useMemo(() => {
        return findLetterContent.split('');
    }, []);

    function handler(e) {
        const targetLetter = e.target;
        if (targetLetter.classList.contains('matchColor_U')) {
            targetLetter.classList.remove('matchColor_U');
        }
        else {
            targetLetter.classList.add('matchColor_U');
        }
        lessonSetStartAction(true);
    }

    //      
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                titleHeading={titleHeading}
                soundSource={soundSource}
                soundVisible={true} titleVisible={true}
            />
            <FindLetterContent findLetter={findLetter} handler={handler} letterBox={letterBox} />
            {/* <div className="lessonContent_findLatter_U_Wrapper">
                {textGameArr.map(item => {
                    let uniq1 = String.fromCharCode(Math.floor((Math.random() * 25) + 65));
                    let uniq2 = String.fromCharCode(Math.floor((Math.random() * 25) + 65));
                    let uniq3 = Math.floor(Math.random() * (999 - 111 + 1)) + 111;
                    let style = {};
                    if (item != ',' && item != ' ') {
                        style = {
                            cursor: 'pointer',
                        }
                    }
                    else {
                        style = {
                            cursor: 'default',
                        }
                    }
                    return (
                        <span key={`${uniq1}${uniq2}${uniq3}`} onClick={item != ',' && item != ' ' ? handler : undefined} style={style} >{item}</span>
                    )
                })}
            </div> */}
        </div >
    );
}

export default Task23;
