import React, { useState, useContext, useEffect } from 'react';
import './Task6.css';
import { Context } from '../../Context';
import { HeadingText } from '../HeadingText/HeadingText';
import clip from './img/clip.svg'
import iconMicrophone from './img/iconMicrophone.png'
import halloweenSongKaraoke from './media/Eng-2021_2_2_4_2.mp4'
import soundSource from './sound/015.mp3'


//    - 
const alphabetModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>  </p>
        </>
    );
};

//    - 
const helpModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>     </p>
        </>
    );
};


//   ( html )
// START
//   -   ( <>     </> )
const lessonHeading = () => (
    <>
        <p>  .  Halloween!</p>
    </>
);
//      ( <>     </> )
const themeHeading = () => (
    <>
        <p>Look at the pictures. Listen to<br />the music and sing (karaoke). </p>
    </>
);
//  title   (  html )
const titleHeading = '      ! ()';


function Task6() {
    const { helpSetIcon, alphabetSetIcon, helpSetModalContent, alphabetSetModalContent } = useContext(Context);
    //     - (  )
    useEffect(() => {
        //  helpSetIcon -    ( - true,  - false)
        helpSetIcon(true);
        //  alphabetSetIcon -   ( - true,  - false)
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, [])





    //      
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                titleHeading={titleHeading}
                soundSource={soundSource}
                soundVisible={true} titleVisible={true}
            />
            <video className="videoScreen" width="560" controls controlsList="nodownload">
                <source src={halloweenSongKaraoke} type='video/mp4' />
            </video>
            {/* <iframe style={{ borderRadius: '10px', margin: '2rem 0 3rem 0' }} width="560" height="315" src="https://www.youtube.com/embed/4jxcWlq3CBg" title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe> */}
            <button className="voiceRecordBtn">
                <img src={iconMicrophone} width="45px" height="45px" alt="" title="   " />
                <img src={clip} width="45px" height="45px" alt="" title="   " />
                <span>    </span>
            </button>

        </div>
    );
}

export default Task6;