import React, { useState } from 'react';
import './Header.css';
import MenuButton from '../FunctionalComponents/MenuButton/MenuButton';
import LessonBar from '../FunctionalComponents/LessonBar/LessonBar';
import CloseButton from '../FunctionalComponents/CloseButton/CloseButton';
import SidePanel from '../Menu/SidePanel';

const itemsMenu = [
    {
        id: "menu_1",
        value: "  .  Halloween!",
        href: 2
    },
    {
        id: "menu_2",
        value: " ! Learn the ABC!",
        href: 7
    },
    {
        id: "menu_3",
        value: " ! Listen to the story!",
        href: 14
    },
    {
        id: "menu_4",
        value: " ! Learn the ABC!",
        href: 18
    },
    {
        id: "menu_5",
        value: "  ! Lets do the task! ",
        href: 24
    },
    {
        id: "menu_6",
        value: ". Exercises.",
        href: 29
    },
    {
        id: "menu_7",
        value: " ! Learn the ABC!",
        href: 31
    },
    {
        id: "menu_8",
        value: " . Test yourself",
        href: 36
    },
];

function Header() {
    const [dropMenuActive, setDropMenuActive] = useState(false);
    const [munuActive, setMenuActive] = useState(false);
    function setActiveDropMenu(active) {
        setDropMenuActive(active);
    };
    function setActiveMenu(active) {
        setMenuActive(active);
    };



 

    return (
        <div className="header">
            <SidePanel active={munuActive} setActiveMenu={setActiveMenu} itemsMenu={itemsMenu} dropMenu={dropMenuActive} setActiveDropMenu={setActiveDropMenu} />
            <MenuButton active={munuActive} setActiveMenu={setActiveMenu} />
            <LessonBar />
            <CloseButton />
        </div>
    );
}

export default Header;



