import React, { useState, useContext, useEffect } from 'react';
import './Task26.css';
import { Context } from '../../Context';
import { HeadingText } from '../HeadingText/HeadingText';

//    - 
const alphabetModalContent = () => {
    return (
        <>
            <h3></h3>
            <br />
            <p>  </p>
        </>
    );
};

//    - 
const helpModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>     </p>
        </>
    );
};


//   ( html )
// START
//   -   ( <>     </> )
const lessonHeading = () => (
    <>
        <p>Lets do the task!   !</p>
    </>
);
//      ( <>     </> )
const themeHeading = () => (
    <>
        <p> to be  , <br />  </p>
    </>
);
//  title   (  html )
const titleHeading = ' to be  ,    ';


const SentenceHeading = ({ setAffirmative, setInterrogative, setNegative, affirmative, interrogative, negative }) => (
    <div className="lessonContent_sentenceHeading">
        <p onClick={() => { setAffirmative(true); setInterrogative(false); setNegative(false) }} className={affirmative ? "targetSentenceColor" : "nonTargetSentenceColor"}>&nbsp;</p>
        <p onClick={() => { setInterrogative(true); setAffirmative(false); setNegative(false) }} className={interrogative ? "targetSentenceColor" : "nonTargetSentenceColor"}>&nbsp;</p>
        <p onClick={() => { setNegative(true); setInterrogative(false); setAffirmative(false) }} className={negative ? "targetSentenceColor" : "nonTargetSentenceColor"} >&nbsp; </p>
    </div>
);
const TargetSentences = ({ sentence, targetWords }) => (
    <div className="lessonContent_sentenceExamples">
        {sentence.map(item => {
            const itemContent = item.content.split(' ');
            return (
                <div key={item.id} className="sentenceExample_examples">
                    {itemContent.map((item, index) => {
                        // let uniq1 = String.fromCharCode(Math.floor((Math.random() * 25) + 65));
                        // let uniq2 = String.fromCharCode(Math.floor((Math.random() * 25) + 65));
                        // let uniq3 = Math.floor(Math.random() * (999 - 111 + 1)) + 111;
                        let style = {};
                        targetWords.map(element => {
                            if (item.replace(/[^\w\s]/gi, '').toLowerCase() === element.replace(/[^\w\s]/gi, '').toLowerCase()) style = { fontWeight: '600', color: '#1D66FF' }
                        })
                        return (
                            <span key={`${item}${index}`} style={style}>{item}&nbsp;</span>
                        )
                    })}
                </div>
            )
        })}
    </div>
);

function Task26() {
    const { helpSetIcon, alphabetSetIcon, helpSetModalContent, alphabetSetModalContent } = useContext(Context);
    const [affirmative, setAffirmative] = useState(true);
    const [interrogative, setInterrogative] = useState(false);
    const [negative, setNegative] = useState(false);
    //     - (  )
    useEffect(() => {
        //  helpSetIcon -    ( - true,  - false)
        helpSetIcon(true);
        //  alphabetSetIcon -   ( - true,  - false)
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, []);

    const affirmativeSentence_targetWords = ['is', 'are', 'am'];
    const affirmativeSentence = [
        {
            id: 'affirmativeSentence_1',
            mediaSource: '#',
            content: 'I am a pupil.'
        },
        {
            id: 'affirmativeSentence_2',
            mediaSource: '#',
            content: 'You are a pupil.'
        },
        {
            id: 'affirmativeSentence_3',
            mediaSource: '#',
            content: 'He is a pupil.'
        },
        {
            id: 'affirmativeSentence_4',
            mediaSource: '#',
            content: 'She is a pupil.'
        },
        {
            id: 'affirmativeSentence_5',
            mediaSource: '#',
            content: 'We are pupils.'
        },
        {
            id: 'affirmativeSentence_6',
            mediaSource: '#',
            content: 'You are pupils.'
        },
        {
            id: 'affirmativeSentence_7',
            mediaSource: '#',
            content: 'They are pupils.'
        },
        {
            id: 'affirmativeSentence_8',
            mediaSource: '#',
            content: 'I am at school now.'
        },
        {
            id: 'affirmativeSentence_9',
            mediaSource: '#',
            content: 'She / He is at school now.'
        },
        {
            id: 'affirmativeSentence_10',
            mediaSource: '#',
            content: 'We / You / They are at school now.'
        }
    ];

    const interrogativeSentence_targetWords = ['am', 'are', 'is'];
    const interrogativeSentence = [
        {
            id: 'interrogativeSentence_1',
            mediaSource: '#',
            content: 'Am I a pupil?'
        },
        {
            id: 'interrogativeSentence_2',
            mediaSource: '#',
            content: 'Are you a pupil?'
        },
        {
            id: 'interrogativeSentence_3',
            mediaSource: '#',
            content: 'Is she a pupil?'
        },
        {
            id: 'interrogativeSentence_4',
            mediaSource: '#',
            content: 'Is he a pupil?'
        },
        {
            id: 'interrogativeSentence_5',
            mediaSource: '#',
            content: 'Are we pupils?'
        },
        {
            id: 'interrogativeSentence_6',
            mediaSource: '#',
            content: 'Are you pupils?'
        },
        {
            id: 'interrogativeSentence_7',
            mediaSource: '#',
            content: 'Are they pupils?'
        },
        {
            id: 'interrogativeSentence_8',
            mediaSource: '#',
            content: 'Am I at school now?'
        },
        {
            id: 'interrogativeSentence_9',
            mediaSource: '#',
            content: 'Is he / she at school now?'
        },
        {
            id: 'interrogativeSentence_10',
            mediaSource: '#',
            content: 'Are we / you / they at school now?'
        }
    ];

    const negativeSentence_targetWords = ['am', 'are', 'is', 'not'];
    const negativeSentence = [
        {
            id: 'negativeSentence_1',
            mediaSource: '#',
            content: 'I am not a pupil.'
        },
        {
            id: 'negativeSentence_2',
            mediaSource: '#',
            content: 'You are not a pupil.'
        },
        {
            id: 'negativeSentence_3',
            mediaSource: '#',
            content: 'She is not a pupil.'
        },
        {
            id: 'negativeSentence_4',
            mediaSource: '#',
            content: 'He is not a pupil.'
        },
        {
            id: 'negativeSentence_5',
            mediaSource: '#',
            content: 'We are not pupils.'
        },
        {
            id: 'negativeSentence_6',
            mediaSource: '#',
            content: 'You are not pupils.'
        },
        {
            id: 'negativeSentence_7',
            mediaSource: '#',
            content: 'They are not pupils.'
        },
        {
            id: 'negativeSentence_8',
            mediaSource: '#',
            content: 'I am not at school now.'
        },
        {
            id: 'negativeSentence_9',
            mediaSource: '#',
            content: 'He / She is not at school now.'
        },
        {
            id: 'negativeSentence_10',
            mediaSource: '#',
            content: 'We / You / They are not at school now.'
        }
    ];


    //      
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                titleHeading={titleHeading}
                // soundSource={soundSource}
                soundVisible={false}
                titleVisible={true}
            />
            <div className="lessonContent_sentenceWrapper">
                <SentenceHeading
                    setAffirmative={setAffirmative}
                    setInterrogative={setInterrogative}
                    setNegative={setNegative}
                    affirmative={affirmative}
                    interrogative={interrogative}
                    negative={negative}
                />
                {affirmative && <TargetSentences sentence={affirmativeSentence} targetWords={affirmativeSentence_targetWords} />}
                {interrogative && <TargetSentences sentence={interrogativeSentence} targetWords={interrogativeSentence_targetWords} />}
                {negative && <TargetSentences sentence={negativeSentence} targetWords={negativeSentence_targetWords} />}

            </div>
        </div>
    );
}

export default Task26;