import React, { useState, useContext, useEffect, useRef } from 'react';
import './Task28.css';
import { Context } from '../../Context';
import { HeadingText } from '../HeadingText/HeadingText';
import soundSource from '../Task27/sound/041.mp3';


//    - 
const alphabetModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>  </p>
        </>
    );
};

//    - 
const helpModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>     </p>
        </>
    );
};


//   ( html )
// START
//   -   ( <>     </> )
const lessonHeading = () => (
    <>
        <p>Lets do the task!   !</p>
    </>
);
//      ( <>     </> )
const themeHeading = () => (
    <>
        <p>Choose the article if necessary</p>
    </>
);
//  title   (  html )
const titleHeading = '   ';

const SentenceWithSelect = ({ choise, setChoise, firstPartSentence, secondPartSentence, lessonSetStartAction }) => (
    <p className="lessonContent_chooseArticle_sentence">{firstPartSentence}&ensp;
        <select className="lessonContent_select" value={choise} onChange={(event) => { setChoise(event.target.value); lessonSetStartAction(true) }} title="  ">
            <option defaultValue value="outChoise">...</option>
            <option value="a">a</option>
            <option value="an">an</option>
        </select>&ensp;{secondPartSentence}.
    </p>
);


function Task28() {
    const { helpSetIcon, alphabetSetIcon, helpSetModalContent, alphabetSetModalContent, lessonSetStartAction, lessonSetResetTask, resetTask, lessonSetCheckTask, checkTask, lessonSetAndAnswer } = useContext(Context);
    //     - (  )
    useEffect(() => {
        //  helpSetIcon -    ( - true,  - false)
        helpSetIcon(true);
        //  alphabetSetIcon -   ( - true,  - false)
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, []);
    const selectContent = useRef(null);
    const [choise1, setChoise1] = useState(undefined);
    const [choise2, setChoise2] = useState(undefined);
    const [choise3, setChoise3] = useState(undefined);
    const [choise4, setChoise4] = useState(undefined);
    const [choise5, setChoise5] = useState(undefined);
    const [choise6, setChoise6] = useState(undefined);


    useEffect(() => {
        if (resetTask === true) {
            const arr = selectContent.current.childNodes;
            const resetStates = () => {
                setChoise1(undefined);
                setChoise2(undefined);
                setChoise3(undefined);
                setChoise4(undefined);
                setChoise5(undefined);
                setChoise6(undefined);
            };
            for (let i = 0; i < arr.length; i++) {
                console.log(arr[i].childNodes[2])
                arr[i].childNodes[2].selectedIndex = 0;
            }
            resetStates();
        }
        lessonSetResetTask(false);
    }, [resetTask]);

    useEffect(() => {
        if (checkTask === true) {
            if (choise1 === 'a' && choise2 === 'a' && choise3 === 'a' && choise4 === 'a' && choise5 === 'an', choise6 === 'a') {
                lessonSetAndAnswer(true);
            }
            else {
                lessonSetAndAnswer(false);
            }
        }
        lessonSetCheckTask(false);
    }, [checkTask]);

    //      
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                titleHeading={titleHeading}
                soundSource={soundSource}
                soundVisible={true} titleVisible={true}
            />
            <div className="lessonContent_chooseArticle" ref={selectContent}>
                <SentenceWithSelect choise={choise1} setChoise={setChoise1} firstPartSentence='This is' secondPartSentence='red umbrella' lessonSetStartAction={lessonSetStartAction} />
                <SentenceWithSelect choise={choise2} setChoise={setChoise2} firstPartSentence='That is' secondPartSentence='green spider' lessonSetStartAction={lessonSetStartAction} />
                <SentenceWithSelect choise={choise3} setChoise={setChoise3} firstPartSentence='That is' secondPartSentence='black hat' lessonSetStartAction={lessonSetStartAction} />
                <SentenceWithSelect choise={choise4} setChoise={setChoise4} firstPartSentence='That is' secondPartSentence='purple witch' lessonSetStartAction={lessonSetStartAction} />
                <SentenceWithSelect choise={choise5} setChoise={setChoise5} firstPartSentence='This is' secondPartSentence='orange pumpkin' lessonSetStartAction={lessonSetStartAction} />
                <SentenceWithSelect choise={choise6} setChoise={setChoise6} firstPartSentence='That is' secondPartSentence='yellow castle' lessonSetStartAction={lessonSetStartAction} />
            </div>
        </div>
    );
}

export default Task28;



