import React, { useContext, useState, useEffect } from 'react';
import './Task7.css';
import { HeadingText } from '../HeadingText/HeadingText';
import { Context } from '../../Context';
import magicTrick from './media/Engl-2021_2_2_4_5.mp4'
// import soundSource from './media/Engl-2021_2_2_4_5.mp4'

//    - 
const alphabetModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>  </p>
        </>
    );
};

//    - 
const helpModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>     </p>
        </>
    );
};

//   ( html )
// START
//   -   ( <>     </> )
const lessonHeading = () => (
    <>
        <p>  .  Halloween!</p>
    </>
);
//      ( <>     </> )
const themeHeading = () => (
    <>
        <p>Do you like surprising your friends? Watch <br />this video to learn how to do a magic trick!</p>
    </>
);
//  title   (  html )
const titleHeading = '    ?   ,  ,   !';

function Task7() {
    const { lessonSetAndAnswer } = useContext(Context);
    const [answer, setAnswer] = useState();
    const { helpSetIcon, alphabetSetIcon, helpSetModalContent, alphabetSetModalContent } = useContext(Context);
    //     - (  )
    useEffect(() => {
        //  helpSetIcon -    ( - true,  - false)
        helpSetIcon(true);
        //  alphabetSetIcon -   ( - true,  - false) 
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, []);




    const styles = ({
        textAlign: 'center'
    });

    //      
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                titleHeading={titleHeading}
                // soundSource={soundSource}
                soundVisible={false} titleVisible={true}
            />
            <video className="videoScreen" width="560" controls controlsList="nodownload">
                <source src={magicTrick} type='video/mp4' />
            </video>
            {/* <iframe style={{ borderRadius: '10px', margin: '2rem 0 3rem 0' }} width="600" src="https://www.youtube.com/embed/LLfDU3pFeI0" title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe> */}
        </div>
    );
}

export default Task7;




