import React, { useState, useContext, useEffect, useMemo } from 'react';
import './Task18.css';
import { Context } from '../../Context';
import { HeadingText } from '../HeadingText/HeadingText';

// import witchSweet from './img/witchSweet_choosePicture.svg';
// import witchGhost from './img/witchGhost_choosePicturesvg.svg';
// import witchPumpkin from './img/witchPumpkin_choosePicture.svg';
import check from './img/check.svg'

import eng1 from './img/eng1.png';
import eng2 from './img/eng2.png';
import eng3 from './img/eng3.png';

import soundSource from './sound/028.mp3';
import soundSourcePlayer from './sound/029.mp3';

//    -  ( <>     </> )
const alphabetModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>  </p>
        </>
    );
};

//    -  ( <>     </> )
const helpModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>     </p>
        </>
    );
};


//   ( html )
// START
//   -   ( <>     </> )
const lessonHeading = () => (
    <>
        <p> !  Listen to the story!</p>
    </>
);
//      ( <>     </> )
const themeHeading = () => (
    <>
        <p>Listen and choose the right picture</p>
    </>
);
//  title   (  html )
const titleHeading = '    ';


function Task18() {
    const { helpSetIcon, alphabetSetIcon, helpSetModalContent, alphabetSetModalContent, lessonSetStartAction, lessonSetResetTask, resetTask, lessonSetCheckTask, checkTask, lessonSetAndAnswer } = useContext(Context);
    //     - (  )
    useEffect(() => {
        //  helpSetIcon -    ( - true,  - false)
        helpSetIcon(true);
        //  alphabetSetIcon -   ( - true,  - false)
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, []);

    const [winState, setWinState] = useState();

    const [checked1, setChecked1] = useState(false);
    const [checked2, setChecked2] = useState(false);
    const [checked3, setChecked3] = useState(false);

    useEffect(() => {
        if (resetTask === true) {
            setChecked3(false);
            setChecked2(false);
            setChecked1(false);
        }
        lessonSetResetTask(false);
    }, [resetTask]);

    useEffect(() => {
        if (checkTask === true) {
            if (winState === 'green') {
                lessonSetAndAnswer(true);
            }
            else {
                lessonSetAndAnswer(false);
            }
        }
        lessonSetCheckTask(false);
    }, [checkTask]);

    function handleToggle(e) {
        const targetPic = e.target;
        setChecked1(false);
        setChecked2(false);
        setChecked3(false);
        checking(targetPic.getAttribute('data'));
        lessonSetStartAction(true);
    }

    function checking(targetPicData) {
        if (targetPicData === 'brown') setChecked1(!checked1); setWinState(targetPicData);
        if (targetPicData === 'green') setChecked2(!checked2); setWinState(targetPicData);
        if (targetPicData === 'red') setChecked3(!checked3); setWinState(targetPicData);
    }


    //      
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                titleHeading={titleHeading}
                soundSource={soundSource}
                soundVisible={true} titleVisible={true}
            />
            <div className="lessonContent_listenAndChoosePicture">
                <div className="lessonContent_player"><audio src={soundSourcePlayer} controls controlsList="nodownload"></audio></div>
                <div className="lessonContent_picturesForChoose">
                    <img className="player_picturesForChoose_img" src={eng1} data="brown" width="30%" onClick={handleToggle} /><img className="checkIcon1" hidden={!checked1} src={check} />
                    <img className="player_picturesForChoose_img" src={eng2} data="green" width="30%" onClick={handleToggle} /><img className="checkIcon2" hidden={!checked2} src={check} />
                    <img className="player_picturesForChoose_img" src={eng3} data="red" width="30%" onClick={handleToggle} /><img className="checkIcon3" hidden={!checked3} src={check} />
                </div>
            </div>
        </div>
    );
}

export default Task18;