import React, { useContext, useEffect } from 'react';
import './Task3.css';
import { HeadingText } from '../HeadingText/HeadingText';
import { Context } from '../../Context';
import speaker from '../assets/img/speaker_volume.svg';
import {
    sound_see,
    sound_bat,
    sound_witch,
    sound_with,
    sound_spider,
    sound_weaving,
    sound_ghost,
    sound_flying,
    sound_pumpkin,
    sound_lying,
    soundSource
} from './sound/index_sound.js'





//    - 
const alphabetModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>  </p>
        </>
    );
};

//    - 
const helpModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>     </p>
        </>
    );
};

//   ( html )
// START
//   -   ( <>     </> )
const lessonHeading = () => (
    <>
        <p>  .  Halloween!</p>
    </>
);
//      ( <>     </> )
const themeHeading = () => (
    <>
        <p>Look, listen and repeat</p>
    </>
);
//  title   (  html )
const titleHeading = ',   ';

function Task3() {
    const { helpSetIcon, alphabetSetIcon, helpSetModalContent, alphabetSetModalContent } = useContext(Context);
    //     - (  )
    useEffect(() => {
        //  helpSetIcon -    ( - true,  - false)
        helpSetIcon(true);
        //  alphabetSetIcon -   ( - true,  - false)
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, [])

    const chartsContent = [
        {
            id: 'chartsContent_1',
            mediaSource: sound_see,
            content: `see [si]  `
        },
        {
            id: 'chartsContent_2',
            mediaSource: sound_bat,
            content: `bat [bt]   `
        },
        {
            id: 'chartsContent_3',
            mediaSource: sound_witch,
            content: `witch [wt]  `
        },
        {
            id: 'chartsContent_4',
            mediaSource: sound_with,
            content: `with [w]  `
        },
        {
            id: 'chartsContent_5',
            mediaSource: sound_spider,
            content: `spider [spad]  `
        },
        {
            id: 'chartsContent_6',
            mediaSource: sound_weaving,
            content: `weaving a net [wiv  net]   `
        },
        {
            id: 'chartsContent_7',
            mediaSource: sound_ghost,
            content: `ghost  [st]  `
        },
        {
            id: 'chartsContent_8',
            mediaSource: sound_flying,
            content: `flying above the hat [fla bv  ht]    `
        },
        {
            id: 'chartsContent_9',
            mediaSource: sound_pumpkin,
            content: `pumpkin [pm(p)kn]  `
        },
        {
            id: 'chartsContent_10',
            mediaSource: sound_lying,
            content: `lying on the mat [la n  mt]    `
        },
    ];

    const playSound = (media) => {
        let audio = new Audio(media);
        audio.play()
    }



    //      
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                titleHeading={titleHeading}
                soundSource={soundSource}
                soundVisible={true} titleVisible={true}
            />
            <div className="wrapperTask3">
                {chartsContent.map(item => (
                    <div key={item.id} className="chartsTask3" onClick={() => playSound(item.mediaSource)}>
                        <span style={{ marginRight: '15px' }} title=""><img style={{ marginBottom: '-5px' }} src={speaker} alt="" /></span><span>{item.content}</span>
                    </div>
                ))}
            </div>

        </div>
    );
}

export default Task3;