import React, { useState, useContext, useEffect, useRef, useMemo } from 'react';
import './Task11.css';
import { HeadingText } from '../HeadingText/HeadingText';
import { Context } from '../../Context';
import soundSource from './sound/036.mp3';


//    - 
const alphabetModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>  </p>
        </>
    );
};

//    - 
const helpModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>     </p>
        </>
    );
};

//   ( html )
// START
//   -   ( <>     </> )
const lessonHeading = () => (
    <>
        <p> ! Learn the ABC!</p>
    </>
);
//      ( <>     </> )
const themeHeading = () => (
    <>
        <p>Drag and put the letters in alphabetical order</p>
    </>
);
//  title   (  html )
const titleHeading = '      ';

function Task11() {

    const { helpSetIcon, alphabetSetIcon, helpSetModalContent, alphabetSetModalContent, lessonSetStartAction, lessonSetResetTask, resetTask, checkTask, lessonSetCheckTask, lessonSetAndAnswer } = useContext(Context);
    // const [finState, setFinState] = useState(false);
    const alphabetDNDLessonDrop = useRef(null);
    const alphabetDNDLessonDrag = useRef(null);
    //     - (  )
    useEffect(() => {
        //  helpSetIcon -    ( - true,  - false)
        helpSetIcon(true);
        //  alphabetSetIcon -   ( - true,  - false)
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, []);



    useEffect(() => {
        for (let i = 0; i < alphabetDNDLessonDrop.current.childNodes.length; i++) {
            if (alphabetDNDLessonDrop.current.childNodes[i].hasChildNodes()) {
                alphabetDNDLessonDrop.current.childNodes[i].firstChild.classList.remove('letterCard_drag_active')
                alphabetDNDLessonDrop.current.childNodes[i].firstChild.classList.add('letterCard_drag')
                alphabetDNDLessonDrag.current.appendChild(alphabetDNDLessonDrop.current.childNodes[i].firstChild)
            }
        }
        lessonSetResetTask(false);
    }, [resetTask])


    useEffect(() => {
        if (checkTask === true) {
            let winCounter = 0;
            for (let i = 0; i < alphabetDNDLessonDrop.current.childNodes.length; i++) {
                if (alphabetDNDLessonDrop.current.childNodes[i].firstChild) {
                    const parentCheckValue = alphabetDNDLessonDrop.current.childNodes[i].attributes.getNamedItem("check-data").value;
                    const childCheckValue = alphabetDNDLessonDrop.current.childNodes[i].firstChild.attributes.getNamedItem("check-data").value;
                    if (parentCheckValue === childCheckValue) {
                        winCounter++;
                    }
                }
            }
            if (winCounter === 26) {
                lessonSetAndAnswer(true);
            }
            else {
                lessonSetAndAnswer(false);
            }
        }
        lessonSetCheckTask(false);
        // lessonSetResetTask(false);
    }, [checkTask]);

    const alphabetDraggable = [
        {
            id: 'al_1',
            letter: 'A a',
        },
        {
            id: 'al_2',
            letter: 'B b',
        },
        {
            id: 'al_3',
            letter: 'C c',
        },
        {
            id: 'al_4',
            letter: 'D d',
        },
        {
            id: 'al_5',
            letter: 'E e',
        },
        {
            id: 'al_6',
            letter: 'F f',
        },
        {
            id: 'al_7',
            letter: 'G g',
        },
        {
            id: 'al_8',
            letter: 'H h',
        },
        {
            id: 'al_9',
            letter: 'I i',
        },
        {
            id: 'al_10',
            letter: 'J j',
        },
        {
            id: 'al_11',
            letter: 'K k',
        },
        {
            id: 'al_12',
            letter: 'L l',
        },
        {
            id: 'al_13',
            letter: 'M m',
        },
        {
            id: 'al_14',
            letter: 'N n',
        },
        {
            id: 'al_15',
            letter: 'O o',
        },
        {
            id: 'al_16',
            letter: 'P p',
        },
        {
            id: 'al_17',
            letter: 'Q q',
        },
        {
            id: 'al_18',
            letter: 'R r',
        },
        {
            id: 'al_19',
            letter: 'S s',
        },
        {
            id: 'al_20',
            letter: 'T t',
        },
        {
            id: 'al_21',
            letter: 'U u',
        },
        {
            id: 'al_22',
            letter: 'V v',
        },
        {
            id: 'al_23',
            letter: 'W w',
        },
        {
            id: 'al_24',
            letter: 'X x',
        },
        {
            id: 'al_25',
            letter: 'Y y',
        },
        {
            id: 'al_26',
            letter: 'Z z',
        },
    ];

    let draggingLetter;
    let elemBelow;

    function mouseDown(event) {
        draggingLetter = event.target;
        draggingLetter.style.cursor = 'grabbing';
        let shiftX = event.clientX - draggingLetter.getBoundingClientRect().left + 10;
        let shiftY = event.clientY - draggingLetter.getBoundingClientRect().top + 10;

        if (draggingLetter.classList.contains('letterCard_drag_active')) {
            draggingLetter.classList.add('letterCard_drag');
            draggingLetter.classList.remove('letterCard_drag_active')
        }

        draggingLetter.style.position = 'absolute';
        draggingLetter.style.zIndex = 1000;
        document.body.appendChild(draggingLetter);

        moveAt(event.pageX, event.pageY);

        function moveAt(pageX, pageY) {
            draggingLetter.style.left = pageX - shiftX + 'px';
            draggingLetter.style.top = pageY - shiftY + 'px';
        }

        elemBelow = document.elementFromPoint(event.clientX, event.clientY);

        function onMouseMove(event) {

            moveAt(event.pageX, event.pageY);

            draggingLetter.hidden = true;
            elemBelow = document.elementFromPoint(event.clientX, event.clientY);
            draggingLetter.hidden = false;

            if (!elemBelow) return;


        }

        document.addEventListener('mousemove', onMouseMove);

        // && elemBelow.childNodes.length === 3
        draggingLetter.onmouseup = function () {
            document.removeEventListener('mousemove', onMouseMove);
            draggingLetter.style.cursor = 'grab';

            if (elemBelow.classList.contains('letterCard_drop') && elemBelow.childNodes.length !== 2) {
                draggingLetter.classList.remove('letterCard_drag');
                draggingLetter.classList.add('letterCard_drag_active');

                draggingLetter.style.position = 'relative ';
                draggingLetter.style.zIndex = null;
                draggingLetter.style.top = null;
                draggingLetter.style.left = null;
                elemBelow.append(draggingLetter);

                lessonSetStartAction(true);

            }
            else {
                draggingLetter.style.position = 'relative ';
                draggingLetter.style.zIndex = null;
                draggingLetter.style.top = null;
                draggingLetter.style.left = null;
                alphabetDNDLessonDrag.current.appendChild(draggingLetter);
            }
        };
    };
    const alphabetReady = useMemo(() => {
        return alphabetDraggable.sort(() => Math.random() > 0.5 ? 1 : -1);
    }, []);


    //      
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                titleHeading={titleHeading}
                soundSource={soundSource}
                soundVisible={true} titleVisible={true}
            />
            <div className="lessonContent_alphabetDND">

                <div className="alphabetDNDLesson" ref={alphabetDNDLessonDrop}>
                    {alphabetDraggable.map(item => (
                        <div key={item.id} className="letterCard_drop" check-data={item.id} ></div>
                    ))}
                </div>

                <div className="alphabetDNDLesson" ref={alphabetDNDLessonDrag} >
                    {alphabetReady.map(item => (

                        <div key={item.id} check-data={item.id} className="letterCard_drag" onMouseDown={mouseDown} hidden={false}><p>{item.letter}</p></div>
                    ))}
                </div>
            </div>
        </div >
    );
}

export default Task11;





