import React, { useState, useContext, useEffect, useMemo, useRef } from 'react';
import './Task38.css';
import { Context } from '../../Context';
import { HeadingText } from '../HeadingText/HeadingText';
import soundSource from './sound/054.mp3';


//    - 
const alphabetModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>  </p>
        </>
    );
};

//    - 
const helpModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>     </p>
        </>
    );
};


//   ( html )
// START
//   -   ( <>     </> )
const lessonHeading = () => (
    <>
        <p> </p>
    </>
);
//      ( <>     </> )
const themeHeading = () => (
    <>
        <p>Match the English and the Russian sentences.</p>
    </>
);
//  title   (  html )
const titleHeading = '    ';

const wordsForMatching = [
    {
        english: {
            data: 'engRu1',
            value: 'This is a white ghost.'
        },
        russian: {
            data: 'engRu1',
            value: '  '
        }
    },
    {
        english: {
            data: 'engRu2',
            value: 'That is a pink castle.'
        },
        russian: {
            data: 'engRu2',
            value: '  .'
        }
    },
    {
        english: {
            data: 'engRu3',
            value: 'Those are orange pumpkins.'
        },
        russian: {
            data: 'engRu3',
            value: '  .'
        }
    },
    {
        english: {
            data: 'engRu4',
            value: 'These are my green spiders.'
        },
        russian: {
            data: 'engRu4',
            value: '   .'
        }
    },
    {
        english: {
            data: 'engRu5',
            value: 'Write a word net.'
        },
        russian: {
            data: 'engRu5',
            value: '  .'
        }
    },



    {
        english: {
            data: 'engRu1',
            value: 'Learn the word witch.'
        },
        russian: {
            data: 'engRu1',
            value: '  .'
        }
    },
    {
        english: {
            data: 'engRu2',
            value: 'Spell the word cat.'
        },
        russian: {
            data: 'engRu2',
            value: '    .'
        }
    },
    {
        english: {
            data: 'engRu3',
            value: 'Count the black bats.'
        },
        russian: {
            data: 'engRu3',
            value: '   .'
        }
    },
    {
        english: {
            data: 'engRu4',
            value: 'Put a hat in your school bag!'
        },
        russian: {
            data: 'engRu4',
            value: '   .'
        }
    },
    {
        english: {
            data: 'engRu5',
            value: 'I am at a party.'
        },
        russian: {
            data: 'engRu5',
            value: '  .'
        }
    }

];


function Task38() {
    const { helpSetIcon, alphabetSetIcon, helpSetModalContent, alphabetSetModalContent, lessonSetStartAction, lessonSetResetTask, resetTask, checkTask, lessonSetCheckTask, lessonSetAndAnswer, startAction } = useContext(Context);
    //     - (  )
    useEffect(() => {
        //  helpSetIcon -    ( - true,  - false)
        helpSetIcon(true);
        //  alphabetSetIcon -   ( - true,  - false)
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, []);

    const [connect_1, setConnect_1] = useState([{ veiw: false, lineState: "1" }, { x1: undefined, y1: undefined, x2: undefined, y2: undefined }]);
    const [connect_2, setConnect_2] = useState([{ veiw: false, lineState: "2" }, { x1: undefined, y1: undefined, x2: undefined, y2: undefined }]);
    const [connect_3, setConnect_3] = useState([{ veiw: false, lineState: "3" }, { x1: undefined, y1: undefined, x2: undefined, y2: undefined }]);
    const [connect_4, setConnect_4] = useState([{ veiw: false, lineState: "4" }, { x1: undefined, y1: undefined, x2: undefined, y2: undefined }]);
    const [connect_5, setConnect_5] = useState([{ veiw: false, lineState: "5" }, { x1: undefined, y1: undefined, x2: undefined, y2: undefined }]);
    // const [connect_6, setConnect_6] = useState([{ veiw: false, lineState: "6" },{x1: undefined,y1: undefined,x2: undefined,y2: undefined}]);

    const [connectLines, setLonnectLines] = useState([[connect_1, setConnect_1], [connect_2, setConnect_2], [connect_3, setConnect_3], [connect_4, setConnect_4], [connect_5, setConnect_5],]); { /*  [connect_6,setConnect_6] */ }

    const [foundPairs, setFoundPairs] = useState([]);
    const [englishActive, setEnglishActive] = useState(undefined);
    const [russianActive, setRussianActive] = useState(undefined);
    const lines = useRef(null);
    const taskWrapper = useRef(null);
    const leftColumn = useRef(null);
    const rightColumn = useRef(null);

    useEffect(() => {
        if (resetTask) {
            const copy = connectLines.map(item => {
                if (item[0][0].veiw === true) {
                    item[0][0].veiw = false;
                    item[0][1].x1 = undefined;
                    item[0][1].y1 = undefined;
                    item[0][1].x2 = undefined;
                    item[0][1].y2 = undefined;
                }
                return item
            });

            leftColumn.current.childNodes.forEach(elem => {
                elem.setAttribute('inpair', false);
                elem.setAttribute('linestate', '0');
                elem.classList.remove('activeBlocksMaching')
            });
            rightColumn.current.childNodes.forEach(elem => {
                elem.setAttribute('inpair', false);
                elem.classList.remove('activeBlocksMaching');
                elem.setAttribute('linestate', '0');
            })

            let arr = foundPairs.slice();
            while (arr.length > 0) {
                arr.pop();
            }
            setFoundPairs(arr);
            setEnglishActive(undefined);
            setRussianActive(undefined);
            lessonSetResetTask(false);
        }
    }, [resetTask])


    useEffect(() => {
        if (checkTask === true) {
            let winCount = 0;
            const winState = foundPairs.map(pair => {
                console.log(pair)
                if (pair.englishActive.attributes.getNamedItem('data').value === pair.russianActive.attributes.getNamedItem('data').value) {
                    winCount++;
                }
            });
            if (winCount === 5) {
                lessonSetAndAnswer(true);
            }
            else {
                lessonSetAndAnswer(false);
            }
        }
        lessonSetCheckTask(false);
    }, [checkTask]);


    useEffect(() => {
        if (russianActive && englishActive) {
            setFoundPairs(value => [...value.filter(pair => pair.englishActive !== englishActive && pair.russianActive !== russianActive), { englishActive, russianActive }]);
            englishActive.setAttribute('inpair', true);
            russianActive.setAttribute('inpair', true);

            const parentPos = taskWrapper.current.getBoundingClientRect();
            const childrenPosLeftColumn = englishActive.getBoundingClientRect();
            const childrenPosRightColumn = russianActive.getBoundingClientRect();

            const relativePosLeftColumn = {};
            const relativePosRightColumn = {};

            relativePosLeftColumn.top = childrenPosLeftColumn.top - parentPos.top;
            relativePosLeftColumn.right = Math.abs(parentPos.right - childrenPosLeftColumn.right);
            relativePosRightColumn.top = childrenPosRightColumn.top - parentPos.top;
            relativePosRightColumn.left = Math.abs(childrenPosRightColumn.left - parentPos.left) + 1.5;

            const x1 = Math.trunc(relativePosLeftColumn.right - (getComputedStyle(leftColumn.current).marginRight).replace(/[^0-9]/g, ""));
            const y1 = Math.trunc(relativePosLeftColumn.top + englishActive.getBoundingClientRect().height / 2);
            const x2 = Math.trunc(relativePosRightColumn.left);
            const y2 = Math.trunc(relativePosRightColumn.top + russianActive.getBoundingClientRect().height / 2);

            const connectNow = connectLines.find(elem => {
                if (!elem[0][0].veiw) return elem;
            });
            const copy = connectNow[0].slice();
            copy[0].veiw = true;
            copy[1].x1 = x1;
            copy[1].y1 = y1;
            copy[1].x2 = x2;
            copy[1].y2 = y2;


            connectNow[1](copy);

            englishActive.setAttribute('lineState', connectNow[0][0].lineState);
            russianActive.setAttribute('lineState', connectNow[0][0].lineState);

            setEnglishActive(undefined);
            setRussianActive(undefined);

            lessonSetStartAction(true)

        }




    }, [russianActive, englishActive]);



    function onClickHandlerEnglish(event) {
        const targetChart = event.target;
        console.log(foundPairs)
        setEnglishActive(targetChart);
        // let count = 0;
        // const finishConnecting = connectLines.find(elem => {
        //   if (elem[0][0].veiw === true) count++;
        // });

        //    if (count === 6) {
        //     setEnglishActive(undefined);
        //     setRussianActive(undefined);
        //        return
        //    }
        if (targetChart.classList.contains('activeBlocksMaching')) {

            if (targetChart.attributes.getNamedItem("inpair").value === "true") {
                const inpairTag = targetChart.attributes.getNamedItem("linestate").value;

                const connectNow = connectLines.find(elem => {
                    if (elem[0][0].lineState === inpairTag) return elem;
                });
                const copy = connectNow[0].slice();
                copy[0].veiw = false;
                copy[1].x1 = undefined;
                copy[1].y1 = undefined;
                copy[1].x2 = undefined;
                copy[1].y2 = undefined;
                connectNow[1](copy);


                const targetPair = foundPairs.find(elem => {
                    if (elem.englishActive === targetChart) {
                        elem.russianActive.setAttribute('inpair', false);
                        elem.englishActive.setAttribute('inpair', false);
                        elem.russianActive.classList.remove('activeBlocksMaching');
                        return elem
                    }

                });

                const newArr = foundPairs.filter(n => targetPair !== n);
                setFoundPairs(newArr)
                setRussianActive(undefined);

            }
            targetChart.classList.remove('activeBlocksMaching')
            setEnglishActive(undefined);
        }
        else {
            targetChart.classList.add('activeBlocksMaching');
            if (targetChart != englishActive) {
                englishActive?.classList.remove('activeBlocksMaching');
            }
        }


    }




    function onClickHandlerRussian(event) {
        const targetChart = event.target;
        setRussianActive(targetChart);
        // let count = 0;
        // const finishConnecting = connectLines.find(elem => {
        //     if (elem[0][0].veiw === true) count++;
        //   });

        //    if (count === 6) {
        //     setEnglishActive(undefined);
        //     setRussianActive(undefined);
        //        return
        //    }
        if (targetChart.classList.contains('activeBlocksMaching')) {
            if (targetChart.attributes.getNamedItem("inpair").value === 'true') {
                const inpairTag = targetChart.attributes.getNamedItem("linestate").value;

                const connectNow = connectLines.find(elem => {
                    if (elem[0][0].lineState === inpairTag) return elem;
                });
                const copy = connectNow[0].slice();
                copy[0].veiw = false;
                copy[1].x1 = undefined;
                copy[1].y1 = undefined;
                copy[1].x2 = undefined;
                copy[1].y2 = undefined;
                connectNow[1](copy);


                const targetPair = foundPairs.find(elem => {
                    if (elem.russianActive === targetChart) {

                        elem.russianActive.setAttribute('inpair', false);
                        elem.englishActive.setAttribute('inpair', false);
                        elem.englishActive.classList.remove('activeBlocksMaching');
                        return elem
                    }

                });
                const newArr = foundPairs.filter(n => targetPair !== n);

                setFoundPairs(newArr)
                setEnglishActive(undefined);
            }
            targetChart.classList.remove('activeBlocksMaching')
            setRussianActive(undefined);
        }
        else {
            targetChart.classList.add('activeBlocksMaching');
            if (targetChart != russianActive) {
                russianActive?.classList.remove('activeBlocksMaching');
            }
        }


    }


    const wordsReady = useMemo(() => {
        const words = wordsForMatching.sort(() => Math.random() > 0.5 ? 1 : -1);
        return words.slice(0, 5)
    }, []);

    const englishWords = useMemo(() => {
        const words = wordsReady.map(word => word.english).sort(() => Math.random() > 0.5 ? 1 : -1);
        return words
    }, []);

    const russianWords = useMemo(() => {
        const words = wordsReady.map(word => word.russian).sort(() => Math.random() > 0.5 ? 1 : -1);
        return words
    }, []);





    //      
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                titleHeading={titleHeading}
                soundSource={soundSource}
                soundVisible={true} titleVisible={true}
            />
            <div className="wrapperBlocksMaching" ref={taskWrapper}>
                <div className="englishLeftColumn" ref={leftColumn}>
                    {englishWords.map((item, index) => (
                        <div key={item.value} className="chartsBlocksMaching adptive_longWords" inpair="false" linestate="0" data={item.data} onClick={(event) => { onClickHandlerEnglish(event, index); }}>
                            <span style={{ pointerEvents: 'none' }}>{item.value}</span>
                        </div>
                    ))}
                </div>



                {connect_1[0].veiw && <svg style={{ zIndex: 10, position: 'absolute', width: '100%', height: '100%', pointerEvents: 'none' }} visibility="visible"><circle style={{ fill: 'blue' }} r="1.2mm" cx={connect_1[1].x1} cy={connect_1[1].y1} /><line x1={connect_1[1].x1} y1={connect_1[1].y1} x2={connect_1[1].x2} y2={connect_1[1].y2} style={{ stroke: 'blue', strokeWidth: '1.5px' }} /><circle style={{ fill: 'blue' }} r="1.2mm" cx={connect_1[1].x2} cy={connect_1[1].y2} /></svg>}
                {connect_2[0].veiw && <svg style={{ zIndex: 10, position: 'absolute', width: '100%', height: '100%', pointerEvents: 'none' }} visibility="visible"><circle style={{ fill: 'blue' }} r="1.2mm" cx={connect_2[1].x1} cy={connect_2[1].y1} /><line x1={connect_2[1].x1} y1={connect_2[1].y1} x2={connect_2[1].x2} y2={connect_2[1].y2} style={{ stroke: 'blue', strokeWidth: '1.5px' }} /><circle style={{ fill: 'blue' }} r="1.2mm" cx={connect_2[1].x2} cy={connect_2[1].y2} /></svg>}
                {connect_3[0].veiw && <svg style={{ zIndex: 10, position: 'absolute', width: '100%', height: '100%', pointerEvents: 'none' }} visibility="visible"><circle style={{ fill: 'blue' }} r="1.2mm" cx={connect_3[1].x1} cy={connect_3[1].y1} /><line x1={connect_3[1].x1} y1={connect_3[1].y1} x2={connect_3[1].x2} y2={connect_3[1].y2} style={{ stroke: 'blue', strokeWidth: '1.5px' }} /><circle style={{ fill: 'blue' }} r="1.2mm" cx={connect_3[1].x2} cy={connect_3[1].y2} /></svg>}
                {connect_4[0].veiw && <svg style={{ zIndex: 10, position: 'absolute', width: '100%', height: '100%', pointerEvents: 'none' }} visibility="visible"><circle style={{ fill: 'blue' }} r="1.2mm" cx={connect_4[1].x1} cy={connect_4[1].y1} /><line x1={connect_4[1].x1} y1={connect_4[1].y1} x2={connect_4[1].x2} y2={connect_4[1].y2} style={{ stroke: 'blue', strokeWidth: '1.5px' }} /><circle style={{ fill: 'blue' }} r="1.2mm" cx={connect_4[1].x2} cy={connect_4[1].y2} /></svg>}
                {connect_5[0].veiw && <svg style={{ zIndex: 10, position: 'absolute', width: '100%', height: '100%', pointerEvents: 'none' }} visibility="visible"><circle style={{ fill: 'blue' }} r="1.2mm" cx={connect_5[1].x1} cy={connect_5[1].y1} /><line x1={connect_5[1].x1} y1={connect_5[1].y1} x2={connect_5[1].x2} y2={connect_5[1].y2} style={{ stroke: 'blue', strokeWidth: '1.5px' }} /><circle style={{ fill: 'blue' }} r="1.2mm" cx={connect_5[1].x2} cy={connect_5[1].y2} /></svg>}
                {/* {connect_6[0].veiw && <svg style={{ zIndex: 10, position: 'absolute', width: '100%', height: '100%', pointerEvents: 'none' }} visibility="visible"><circle style={{ fill: 'blue' }} r="1.2mm" cx={connect_6[1].x1} cy={connect_6[1].y1} /><line x1={connect_6[1].x1} y1={connect_6[1].y1} x2={connect_6[1].x2} y2={connect_6[1].y2} style={{ stroke: 'blue', strokeWidth: '1.5px' }} /><circle style={{ fill: 'blue' }} r="1.2mm" cx={connect_6[1].x2} cy={connect_6[1].y2} /></svg>} */}



                <div className="russionRightColumn" ref={rightColumn}>
                    {russianWords.map((item, index) => (
                        <div key={item.value} className="chartsBlocksMaching adptive_longWords" inpair="false" linestate="0" data={item.data} onClick={(event) => { onClickHandlerRussian(event, index) }}>
                            <span style={{ pointerEvents: 'none' }} >{item.value}</span>
                        </div>
                    ))}
                </div>
            </div>

        </div>
    );
}

export default Task38;



