import React, { useState, useContext, useEffect, useRef, useMemo } from 'react';
import './Task16.css';
import { Context } from '../../Context';
import { HeadingText } from '../HeadingText/HeadingText';
import {
    castle,
    cat,
    ghost,
    hat,
    pumpkin,
    witch,

    castle1,
    cat1,
    ghost1,
    hat1,
    pumpkin1,
    witch1
} from './img/index_matchPictures.js';
import soundSource from './sound/026.mp3';


//    -  ( <>     </> )
const alphabetModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>  </p>
        </>
    );
};

//    -  ( <>     </> )
const helpModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>     </p>
        </>
    );
};


//   ( html )
// START
//   -   ( <>     </> )
const lessonHeading = () => (
    <>
        <p> !  Listen to the story!</p>
    </>
);
//      ( <>     </> )
const themeHeading = () => (
    <>
        <p>Match the pictures and the words</p>
    </>
);
//  title   (  html )
const titleHeading = '   ';

const picturesForMatching = [
    {
        id: 'picturesForMatching_1',
        data: 'hat',
        src: hat1
    },
    {
        id: 'picturesForMatching_3',
        data: 'castle',
        src: castle1
    },
    {
        id: 'picturesForMatching_6',
        data: 'witch',
        src: witch1
    },
    {
        id: 'picturesForMatching_4',
        data: 'ghost',
        src: ghost1
    },
    {
        id: 'picturesForMatching_5',
        data: 'pumpkin',
        src: pumpkin1
    },
    {
        id: 'picturesForMatching_2',
        data: 'cat',
        src: cat1
    },


];
const wordsForMatching = [
    {
        id: 'wordsForMatching_2',
        data: 'cat',
        value: 'cat'
    },
    {
        id: 'wordsForMatching_3',
        data: 'castle',
        value: 'castle'
    },
    {
        id: 'wordsForMatching_4',
        data: 'ghost',
        value: 'ghost'
    },
    {
        id: 'wordsForMatching_5',
        data: 'pumpkin',
        value: 'pumpkin'
    },
    {
        id: 'wordsForMatching_6',
        data: 'witch',
        value: 'witch'
    },
    {
        id: 'wordsForMatching_1',
        data: 'hat',
        value: 'hat'
    },

];

function Task16() {
    const { helpSetIcon, alphabetSetIcon, helpSetModalContent, alphabetSetModalContent, lessonSetStartAction, lessonSetResetTask, resetTask, lessonSetCheckTask, checkTask, lessonSetAndAnswer } = useContext(Context);
    //     - (  )
    useEffect(() => {
        //  helpSetIcon -    ( - true,  - false)
        helpSetIcon(true);
        //  alphabetSetIcon -   ( - true,  - false)
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, []);
    const pictureCard = useRef(null);
    const draggingCard = useRef(null);


    useEffect(() => {
        if (resetTask === true) {
            for (let i = 0; i < pictureCard.current.childNodes.length; i++) {
                if (pictureCard.current.childNodes[i].childNodes[1]) {
                    const draggingWord = pictureCard.current.childNodes[i].childNodes[1];
                    pictureCard.current.childNodes[i].removeChild(draggingWord);
                    draggingCard.current.appendChild(draggingWord);
                    draggingWord.classList.remove('picturesForMatching_wordCard_active');
                }
            }
        }
        lessonSetResetTask(false);
    }, [resetTask]);

    useEffect(() => {
        if (checkTask === true) {
            const cardsWithWords = wordsForMatching.length;
            let allMached = 0;
            let winCount = 0;
            for (let i = 0; i < pictureCard.current.childNodes.length; i++) {
                const picture = pictureCard.current.childNodes[i].attributes.getNamedItem("data").value;
                const word = pictureCard.current.childNodes[i].childNodes[1]?.attributes.getNamedItem("data").value;
                // if (pictureCard.current.childNodes[i].childNodes[1] != undefined) {
                //     allMached++;
                // }
                if (picture === word) {
                    winCount++;
                }
            }
            if (cardsWithWords === winCount) {
                lessonSetAndAnswer(true);
            }
            else {
                lessonSetAndAnswer(false);
            }
        }
        lessonSetCheckTask(false);
    }, [checkTask]);




    let draggingLetter;
    let elemBelow;
    function mouseDown(event) {
        draggingLetter = event.target;
        draggingLetter.style.cursor = 'grabbing';

        let shiftX = event.clientX - draggingLetter.getBoundingClientRect().left + 6;
        let shiftY = event.clientY - draggingLetter.getBoundingClientRect().top + 6;

        if (draggingLetter.classList.contains('picturesForMatching_wordCard_active')) {
            draggingLetter.classList.remove('picturesForMatching_wordCard_active')
        }

        draggingLetter.style.position = 'absolute';
        draggingLetter.style.zIndex = 1000;
        document.body.appendChild(draggingLetter);

        moveAt(event.pageX, event.pageY);

        function moveAt(pageX, pageY) {
            draggingLetter.style.left = pageX - shiftX + 'px';
            draggingLetter.style.top = pageY - shiftY + 'px';
        }

        elemBelow = document.elementFromPoint(event.clientX, event.clientY);

        function onMouseMove(event) {

            moveAt(event.pageX, event.pageY);

            draggingLetter.hidden = true;
            elemBelow = document.elementFromPoint(event.clientX, event.clientY);
            draggingLetter.hidden = false;

            if (!elemBelow) return;


        }

        document.addEventListener('mousemove', onMouseMove);

        // && elemBelow.childNodes.length === 3
        draggingLetter.onmouseup = function () {
            lessonSetStartAction(true);

            document.removeEventListener('mousemove', onMouseMove);
            draggingLetter.style.cursor = 'grab'
            if (elemBelow.classList.contains('picturesForMatching_pictureCard') && elemBelow.childNodes.length !== 2) {
                // draggingLetter.classList.remove('picturesForMatching_pictureCard');
                draggingLetter.classList.add('picturesForMatching_wordCard_active');

                draggingLetter.style.position = 'relative ';
                draggingLetter.style.zIndex = null;
                draggingLetter.style.top = null;
                draggingLetter.style.left = null;
                elemBelow.appendChild(draggingLetter);

            }
            else {
                draggingLetter.style.position = 'relative ';
                draggingLetter.style.zIndex = null;
                draggingLetter.style.top = null;
                draggingLetter.style.left = null;
                draggingCard.current.appendChild(draggingLetter);
            }
        };

    };

    const pictures = useMemo(() => {
        return picturesForMatching.sort(() => Math.random() > 0.5 ? 1 : -1);
    }, []);

    const words = useMemo(() => {
        return wordsForMatching.sort(() => Math.random() > 0.5 ? 1 : -1);
    }, []);

    //      
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                titleHeading={titleHeading}
                soundSource={soundSource}
                soundVisible={true} titleVisible={true}
            />

            <div className="lessonContent_picturesForMatching">
                <div className="picturesForMatching_pictures" ref={pictureCard}>
                    {pictures.map(item => (
                        <div key={item.id} className="picturesForMatching_pictureCard" data={item.data}><img src={item.src} alt="" /></div>
                    ))}
                </div>

                <div className="picturesForMatching_words" ref={draggingCard}>
                    {words.map(item => (
                        <div key={item.id} className="picturesForMatching_wordCard" data={item.data} onMouseDown={mouseDown} hidden={false}><p>{item.value}</p></div>
                    ))}
                </div>

            </div>

        </div>
    );
}

export default Task16;