const ButtonLeft = ({ onClick }) => (
    <div className="textStartContent_button leftButton" onClick={onClick}><img src={leftArrow} style={{ margin: "15px 0 0 18px" }} alt="" /></div>
);
const ButtonRight = ({ onClick }) => (
    <div className="textStartContent_button rightButton" onClick={onClick}><img src={rightArrow} style={{ margin: "15px 0 0 20px" }} alt="" /></div>
);
const CounterTextActual = ({ startTextNow, textStartContentNumber }) => (
    <div className="textStartContent_textNoWCounter">
        <span className="textStartContent_textNoWCounter__actual">{startTextNow}</span>/{textStartContentNumber}
    </div>
);


//   ( html )
// START
//   -   ( <>     </> )
const lessonHeading = () => (
    <p>  .  Halloween!</p>
);
//      ( <>     </> )
const themeHeading = () => (
    <>
        <p>Do you know? What is Halloween for kids?</p>
    </>
);
//  title   (  html )
const titleHeading = ' ?     ?';



function Task1() {
    const textStartContent = [<TextStartContent1 />, <TextStartContent2 />, <TextStartContent3 />, <TextStartContent4 />];
    const textStartContentNumber = textStartContent.length;
    const [startTextNow, setStartTextNow] = useState(1);
    const { helpSetIcon, alphabetSetIcon } = useContext(Context);
    //     - (  )
    useEffect(() => {
        //  helpSetIcon -    ( - true,  - false)
        helpSetIcon(false);
        //  alphabetSetIcon -   ( - true,  - false)
        alphabetSetIcon(false);
    }, [])


    const actualStartTextNow = startTextNow - 1;

    const rightClickHandler = () => {
        const nextSatrtText = startTextNow + 1;
        if (nextSatrtText > textStartContentNumber) return;
        setStartTextNow(nextSatrtText)
    }

    const leftClickHandler = () => {
        const nextSatrtText = startTextNow - 1;
        if (startTextNow === 1) return;
        setStartTextNow(nextSatrtText)
    }

    //      
    return (
        <div className="lessonContent">
            <div className="lessonContent_startContent">
                <HeadingText
                    lessonHeading={lessonHeading()}
                    themeHeading={themeHeading()}
                    titleHeading={titleHeading}
                    // soundSource={soundSource}
                    soundVisible={false} titleVisible={true}
                />
                <div className="textStartContent">
                    <ButtonLeft onClick={leftClickHandler} />
                    <div className="textStartContent_text">
                        {textStartContent[actualStartTextNow]}
                    </div>
                    <ButtonRight onClick={rightClickHandler} />
                </div>
                <CounterTextActual textStartContentNumber={textStartContentNumber} startTextNow={startTextNow} />
            </div>
            <FlyingBatLeftSide />
            <FlyingBatRightSide />
            <PimpkinRightSide />
            <PimpkinLeftSide />
        </div>
    );
}

export default Task1;