import React, { useContext, useState, useEffect } from 'react';
import './Task9.css';
import { HeadingText } from '../HeadingText/HeadingText';
import { Context } from '../../Context';
import abcSong from './media/ABC_song.mp4';
import soundSource from './sound/059_abc_song.mp3';


//    - 
const alphabetModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>  </p>
        </>
    );
};

//    - 
const helpModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>     </p>
        </>
    );
};

//   ( html )
// START
//   -   ( <>     </> )
const lessonHeading = () => (
    <>
        <p> ! Learn the ABC! </p>
    </>
);
//      ( <>     </> )
const themeHeading = () => (
    <>
        <p>Look and listen to the ABC song.</p>
    </>
);
//  title   (  html )
const titleHeading = '    ABC.';


function Task9() {
    const { lessonSetAndAnswer } = useContext(Context);
    const [answer, setAnswer] = useState();
    const { helpSetIcon, alphabetSetIcon, helpSetModalContent, alphabetSetModalContent } = useContext(Context);
    //     - (  )
    useEffect(() => {
        //  helpSetIcon -    ( - true,  - false)
        helpSetIcon(true);
        //  alphabetSetIcon -   ( - true,  - false) 
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, []);




    //      
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                titleHeading={titleHeading}
                soundSource={soundSource}
                soundVisible={true} titleVisible={true}
            />
            <video className="videoScreen" width="600" controls controlsList="nodownload">
                <source src={abcSong} type='video/mp4' />
            </video>
            {/* <iframe style={{ borderRadius: '10px', margin: '2rem 0 3rem 0' }} width="560" height="315" src="https://www.youtube.com/embed/cR-Qr1V8e_w" title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe> */}
        </div>
    );
}

export default Task9; 