import React, { useState, useContext, useEffect, useRef, useMemo } from 'react';
import './Task17.css';
import { Context } from '../../Context';
import { HeadingText } from '../HeadingText/HeadingText';
import imgCat from './img/catInGlasses.svg';
import soundSource from './sound/027.mp3';


//    -  ( <>     </> )
const alphabetModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>  </p>
        </>
    );
};

//    -  ( <>     </> )
const helpModalContent = () => {
    return (
        <>
            <h3> </h3>
            <br />
            <p>     </p>
        </>
    );
};


//   ( html )
// START
//   -   ( <>     </> )
const lessonHeading = () => (
    <>
        <p> !  Listen to the story!</p>
    </>
);
//      ( <>     </> )
const themeHeading = () => (
    <>
        <p>Look at the picture and fill in the table.</p>
    </>
);
//  title   (  html )
const titleHeading = '     .';


function Task17() {
    const { helpSetIcon, alphabetSetIcon, helpSetModalContent, alphabetSetModalContent, lessonSetStartAction, lessonSetResetTask, resetTask, lessonSetCheckTask, checkTask, lessonSetAndAnswer } = useContext(Context);
    //     - (  )
    useEffect(() => {
        //  helpSetIcon -    ( - true,  - false)
        helpSetIcon(true);
        //  alphabetSetIcon -   ( - true,  - false)
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, []);
    const startForDrop = useRef(null);
    const tablesForDrop = useRef(null);

    useEffect(() => {
        if (resetTask === true) {
            for (let i = 0; i < tablesForDrop.current.childNodes.length; i++) {
                while (tablesForDrop.current.childNodes[i].childNodes.length > 1) {
                    const draggingElem = tablesForDrop.current.childNodes[i].childNodes[1];
                    tablesForDrop.current.childNodes[i].removeChild(draggingElem);
                    draggingElem.style.border = 'none';
                    startForDrop.current.appendChild(draggingElem);
                }
            }
        }
        lessonSetResetTask(false);
    }, [resetTask]);

    useEffect(() => {
        if (checkTask === true) {
            const cardsWithWords = cardForDrag.length;
            let winCount = 0;
            for (let i = 0; i < tablesForDrop.current.childNodes.length; i++) {
                if (tablesForDrop.current.childNodes[i].childNodes.length > 1) {
                    for (let j = 0; j < tablesForDrop.current.childNodes[i].childNodes.length; j++) {
                        if (tablesForDrop.current.childNodes[i].childNodes[j + 1] != undefined
                            &&
                            tablesForDrop.current.childNodes[i].attributes.getNamedItem("data").value
                            ===
                            tablesForDrop.current.childNodes[i].childNodes[j + 1].attributes.getNamedItem("data").value) {

                            winCount++;
                        }
                    }
                }
            }
            console.log(winCount);
            if (winCount === cardsWithWords) {
                lessonSetAndAnswer(true);
            }
            else {
                lessonSetAndAnswer(false);
            }
        }
        lessonSetCheckTask(false);
    }, [checkTask]);

    const fillTableForDrop = [
        {
            id: 'fillTableHeaders_1',
            data: 'this',
            color: '#507CEA',
            value: 'This is'
        },
        {
            id: 'fillTableHeaders_2',
            data: 'that',
            color: '#9450EA',
            value: 'That is'
        },
        {
            id: 'fillTableHeaders_3',
            data: 'these',
            color: '#EA9A50',
            value: 'These are'
        },
        {
            id: 'fillTableHeaders_4',
            data: 'those',
            color: '#EA5050',
            value: 'Those are'
        },
    ];

    const cardForDrag = [
        {
            id: 'cardForDrag_1',
            data: 'that',
            value: 'One black cat'
        },
        {
            id: 'cardForDrag_2',
            data: 'this',
            value: 'One purple hat'
        },
        {
            id: 'cardForDrag_3',
            data: 'these',
            value: 'Seven spiders'
        },
        {
            id: 'cardForDrag_4',
            data: 'that',
            value: 'One castle'
        },
        {
            id: 'cardForDrag_5',
            data: 'these',
            value: 'Four orange pumpkins'
        },
        {
            id: 'cardForDrag_6',
            data: 'those',
            value: 'Three witches Nine bats'
        },

    ];

    let draggingLetter;
    let elemBelow;
    function mouseDown(event) {

        // const onMarginCorrection = parseInt(getComputedStyle(event.target).margin.match(/\d+/));
        draggingLetter = event.target;
        draggingLetter.style.cursor = 'grabbing'

        let shiftX = event.clientX - draggingLetter.getBoundingClientRect().left + 5;
        let shiftY = event.clientY - draggingLetter.getBoundingClientRect().top + 5;

        if (draggingLetter.classList.contains('lessonContent_CardsForDrag_card_active')) {
            draggingLetter.classList.remove('lessonContent_CardsForDrag_card_active');
            draggingLetter.style.border = 'none';
        }

        draggingLetter.style.position = 'absolute';
        draggingLetter.style.zIndex = 1000;
        document.body.appendChild(draggingLetter);

        moveAt(event.pageX, event.pageY);

        function moveAt(pageX, pageY) {
            draggingLetter.style.left = pageX - shiftX + 'px';
            draggingLetter.style.top = pageY - shiftY + 'px';
        }

        elemBelow = document.elementFromPoint(event.clientX, event.clientY);

        function onMouseMove(event) {

            moveAt(event.pageX, event.pageY);

            draggingLetter.hidden = true;
            elemBelow = document.elementFromPoint(event.clientX, event.clientY);
            draggingLetter.hidden = false;

            if (!elemBelow) return;


        }

        document.addEventListener('mousemove', onMouseMove);

        // && elemBelow.childNodes.length === 3
        draggingLetter.onmouseup = function () {
            document.removeEventListener('mousemove', onMouseMove);
            draggingLetter.style.cursor = 'grab';

            if (elemBelow.parentElement.classList.contains('lessonContent_CardsForDrop_card')) {
                elemBelow = elemBelow.parentElement;
            }
            if (elemBelow.classList.contains('lessonContent_CardsForDrop_card')) {
                draggingLetter.classList.add('lessonContent_CardsForDrag_card_active');
                const border = `2px solid ${elemBelow.attributes.getNamedItem("data-color").value}`
                draggingLetter.style.border = border;
                draggingStyleRemoover(elemBelow, draggingLetter);
                lessonSetStartAction(true);
            }
            else {
                draggingStyleRemoover(startForDrop.current, draggingLetter);

            }


        };

    };

    const draggingStyleRemoover = (onDropElem, draggingElem) => {
        draggingElem.style.position = 'relative ';
        draggingElem.style.zIndex = null;
        draggingElem.style.top = null;
        draggingElem.style.left = null;
        onDropElem.appendChild(draggingElem);
    }

    const sentence = useMemo(() => {
        return cardForDrag.sort(() => Math.random() > 0.5 ? 1 : -1);
    }, []);

    const table = useMemo(() => {
        return fillTableForDrop.sort(() => Math.random() > 0.5 ? 1 : -1);
    }, []);

    //      
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                titleHeading={titleHeading}
                soundSource={soundSource}
                soundVisible={true} titleVisible={true}
            />
            <div className="lessonContent_fillTable">
                <div className="lessonContent_fillTable_img">
                    <img src={imgCat} alt="" />
                </div>
                <div className="lessonContent_tableForFill">
                    <div className="lessonContent_CardsForDrop" ref={tablesForDrop}>
                        {table.map(item => {
                            const style = {
                                backgroundColor: item.color,
                                color: 'white'
                            }
                            return (
                                <div key={item.id} className="lessonContent_CardsForDrop_card" style={style} data={item.data} data-color={item.color}>
                                    <p>{item.value}</p>
                                    {/* <p style={{ color: 'black' }}></p> */}
                                </div>
                            )
                        })}
                    </div>
                    <div className="lessonContent_CardsForDrag" ref={startForDrop}>
                        {sentence.map(item => (
                            <div key={item.id} className="lessonContent_CardsForDrag_card" data={item.data} onMouseDown={mouseDown} hidden={false}><p>{item.value}</p></div>
                        ))}
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Task17;